import BScroll from 'better-scroll'
export default {
	name: 'slide',
	props: {
		probeType: { 
			/**
			 * ,0,123
			 * 1.scroll 
			 * 2. scroll 
			 * 3. momentum  scroll 
			 */
			type: Number,
			default: 2
		},
		scrollX: {  // true , eventPassthrough  'horizontal' 
			type: Boolean,
			default: true
		},
		scrollY: {  
			type: Boolean,
			default: false
		},
		stopPropagation: {  
			type: Boolean,
			default: true
		},

		snap: {
			type: [Boolean,Object],
			default: function(){
				return {
					threshold: .3,
					speed: 400,
				}
			}
		},
		currentPageIndex: {
			type: Number,
			default: 0
		},
	},
	mounted(){
		// DOMbetter-scroll
		this.$nextTick(() => { 
			this._initScroll();		 
		});
	},
	methods: {
		_initScroll() {			
			if (!this.$refs.slide) {
				return;
			}
			var u = navigator.userAgent;
			var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android
			var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios
			let clickType;
			if(isAndroid){
				clickType = true;
			}else{
				clickType = false;
			}
			// better-scroll
			this.slide = new BScroll(this.$refs.slide, {
				probeType: this.probeType,
				scrollX: this.scrollX,
				scrollY: this.scrollY,
				click:clickType,
				// stopPropagation: this.stopPropagation,
				snap: this.snap,
				momentum:false,  // , false,
				
			})
			// tab_content  
			if(this.$refs.slideContent.getElementsByClassName('tab_content').length > 1 && this.scrollX){
				this.$refs.slideContent.style.width = this.$refs.slideContent.getElementsByClassName('tab_content').length * 100 + 'vw';
				this.slide.refresh();
				this.slide.on('scrollEnd',(pos) => {
					this.slide.refresh();
					if(Math.abs(pos.x) >= 0 && Math.abs(pos.y) <= 0){
						this._onScrollEnd();
					}
					
				})
			}
			
		},
		_onScrollEnd() {
			let pageIndex = this.slide.getCurrentPage().pageX;
			this.$emit('update:currentPageIndex', pageIndex);
		},

		disable() {	// better-scrollDOM  touchstarttouchmovetouchend
			// better-scrolldisable
			this.slide && this.slide.disable()
		},
		enable() {	// better-scroll,  
			// better-scrollenable
			this.slide && this.slide.enable()
		},
		refresh() {
			// better-scrollrefresh
			this.slide && this.slide.refresh()
		},
		goToPage(x, y, time, easing) {
			// better-scrollnext
			this.slide && this.slide.goToPage(x, y, time, easing)
		},


	},
}



